<template>
  <div>
    <div class="table">
      <ul v-if="ratings.tab">
        <router-link tag="li" :to="{name:'ratingAll'}">全部</router-link>
        <router-link tag="li" :to="{name:'ratingImg'}">有图</router-link>
        <router-link tag="li" :to="{name:'ratingDian'}"><span class="font_family icon-changyonglogo42"></span>{{ratings.tab[2].comment_score_title}}</router-link>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "RatingTable",
      props:["ratings"],
    }
</script>

<style scoped>
.table{
  display: flex;
  justify-content: center;
  padding: .2rem;
}
.table ul{
  overflow: hidden;
  border: .01rem solid #ffa722;
  border-radius: .06rem;
  display: flex;
  color: #ffa722;
  width: 100%;
  font-weight: 500;
  justify-content: space-around;
}
.table ul li{
  padding: .16rem 0;
  flex: 1;
  text-align: center;
  font-size: .26rem;
  display: flex;
  justify-content: center;
}
.table ul li span{
  font-size: .27rem;
  vertical-align: middle;
  float: left;
}
.table ul li:nth-child(2){
  border-left: .01rem solid #ffa722;
  border-right: .01rem solid #ffa722;
}
.table ul .active{
  color: #444;
  background-color: #ffa722;
}
</style>



