<template>
  <div class="r-right">
    <transition name="move">
      <div class="jian" @click.stop="jianShao" v-show="item.count">
        <span class="font_family icon-remove_circle_outlin"></span>
      </div>
    </transition>
    <div class="shuliang" v-show="item.count">{{item.count}}</div>
    <div class="jia" @click.stop="tianJia">
      <span class="font_family icon-add_circle"></span>
      <div class="bg"></div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
    export default {
        name: "cartcontrol",
      props:["item"],
      methods:{
        tianJia(){
          if (!this.item.count){
            Vue.set(this.item,"count",1)
          }else{
            this.item.count++;
          }
        },
        jianShao(){
          this.item.count--;
        },
      },
    }
</script>

<style scoped>
  .r-right{
    position: absolute;
    right: .2rem;
    bottom: .2rem;
    display: flex;
    z-index: 9;
    justify-content: space-between;
  }
  .r-right .shuliang{
    padding: .08rem .2rem;
    font-size: .3rem;
    font-weight: 500;
  }
  .r-right .jia span{
    font-size: .45rem;
    color: #ffbb22;
  }
  .r-right .jia .bg{
    width: .3rem;
    height: .3rem;
    background-color: #444;
    border-radius: 50%;
    position: absolute;
    top: 0.06rem;
    right: .08rem;
    z-index: -1;
  }
  .r-right .jian span{
    font-size: .45rem;
    color: #b4b4b4;
  }
  .move-enter-active,.move-leave-active{
    transition: all 0.3s linear;
  }
  .move-enter,.move-leave-to{
    transform: translateX(.2rem) rotate(180deg);
  }
</style>



