<template>
  <transition name="move">
    <div class="details" v-show="showFlag" ref="details">
      <div>
      <div class="xiangqing">
        <div class="top">
          <div>
            <span class="font_family icon-close" @click="onDetails"></span>
          </div>
          <div>
            <span class="font_family icon-fenxiang"></span>
            <span class="font_family icon-gengduo"></span>
          </div>
        </div>
        <div class="x-img">
          <img :src="food.picture">
        </div>
        <div class="x-txt">
          <div>
            <h5>{{food.name}}</h5>
            <p>{{food.month_saled_content}}</p>
            <div class="foodImg" v-if="food.product_label_picture"><img :src="food.product_label_picture"></div>
            <p><span>¥{{food.min_price}}</span>/{{food.unit}}</p>
          </div>
          <div>
            <div v-show="food.count">
              <cartcontrol :item="food"></cartcontrol>
            </div>
            <div v-show="!food.count" class="xuan" @click="clickXuan">选规格</div>
          </div>
        </div>
      </div>
      <div class="pingjia" v-if="food.rating">
        <div class="p-top">
          <div>{{food.rating.title}}<span>({{food.rating.like_ratio_desc}}<i>{{food.rating.like_ratio}}</i>)</span></div>
          <div>{{food.rating.snd_title}}<span class="font_family keyboard_arrow_right"></span>></div>
        </div>
        <ul>
          <li v-for="(item,index) in food.rating.comment_list" :key="index">
            <div class="img">
              <img :src="item.user_icon">
            </div>
            <div class="txt">
              <div>
                <span>{{item.user_name}}</span>
                <span>{{item.comment_time}}</span>
              </div>
              <p>{{item.comment_content}}</p>
            </div>
          </li>
        </ul>
      </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Cartcontrol from './cartcontrol'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
    export default {
        name: "ShopDetails",
      props:["food"],
      components:{
          "cartcontrol":Cartcontrol,
      },
      data(){
          return {
            showFlag: false,
          }
      },
      methods:{
        showView(){
          this.showFlag = true;
          this.$nextTick(() => {
            if(!this.shopScroll){
              this.shopScroll = new BScroll(this.$refs.details,{click:true})
            }else{
              this.shopScroll.refresh();
            }
          })
        },
        onDetails(){
          this.showFlag = false;
        },
        clickXuan(){
          Vue.set(this.food,"count",1);
        },
      },
    }
</script>

<style scoped>
img{
  width: 100%;
}
.move-enter-active,.move-leave-active{
  transition: all 0.5s ease;
}
.move-enter,.move-leave-to{
  transform: translateX(100%);
}
.details{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 1rem;
  background-color: #fff;
  width: 100%;
}
.details .top{
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: .2rem 0;
  position: absolute;
  top: 0;
  left: 0;
}
.details .top span{
  display: inline-block;
  width: .45rem;
  height: .45rem;
  border-radius: 50%;
  background-color: rgba(0,0,0,.5);
  text-align: center;
  line-height: .45rem;
  color: #fff;
}
.details .top div{
  padding: 0 .2rem;
}
.details .top div:nth-child(1) span{
  font-size: .4rem;
}
.details .top div:nth-child(2) span:nth-child(1){
  font-size: .26rem;
  margin-right: .2rem;
}
.xiangqing{
  border-bottom: .2rem solid #f7f5f7;
  position: relative;
}
.xiangqing .x-img{
  max-height: 7.5rem;
  width: 100%;
  overflow: hidden;
}
.xiangqing .x-txt{
  display: flex;
  justify-content: space-between;
  padding: .2rem;
  color: #444;
}
.xiangqing .x-txt div:nth-child(1) h5{
  font-size: .3rem;
  font-weight: bold;
  padding-bottom: .15rem;
}
.xiangqing .x-txt div:nth-child(1) p{
  font-size: .24rem;
  font-weight: 500;
  color: #999;
  padding-bottom: .1rem;
}
.xiangqing .x-txt div:nth-child(1) span{
  font-size: .35rem;
  color: #fb2c2c;
}
.xiangqing .x-txt div:nth-child(2){
  display: flex;
  align-items: flex-end;
}
.xiangqing .x-txt div:nth-child(2) .xuan{
  padding: .15rem .3rem;
  border-radius: .5rem;
  background-color: #ffc740;
  font-size: .26rem;
  font-weight: 500;
}
.xiangqing .x-txt .foodImg{
  width: 1.3rem;
  margin-bottom: .16rem;
}
.pingjia .p-top{
  display: flex;
  justify-content: space-between;
  padding: .2rem;
  font-size: .26rem;
}
.pingjia .p-top div:nth-child(1){
  font-weight: 500;
  color: #444;
}
.pingjia .p-top div:nth-child(1) span{
  font-size: .24rem;
}
.pingjia .p-top div:nth-child(1) span i{
  color: #fb2c2c;
}
.pingjia .p-top div:nth-child(2){
  color: #666;
  font-size: .24rem;
  padding-top: .04rem;
}
.pingjia .p-top div:nth-child(2) span{
  margin-left: .1rem;
  vertical-align: middle;
}
.pingjia ul li{
  display: flex;
  padding: .2rem .2rem .3rem 0;
  justify-content: space-between;
  border-bottom: .01rem solid #efefef;
  margin-left: .2rem;
}
.pingjia ul li .img{
  width: .8rem;
  height: .8rem;
  border-radius: 2rem;
  background-color: #e8e8e8;
  overflow: hidden;
}
.pingjia ul li .txt{
  padding-left: .2rem;
  flex: 1;
  padding-top: .1rem;
}
.pingjia ul li .txt div{
  display: flex;
  justify-content: space-between;
  color: #666;
  font-size: .24rem;
}
.pingjia ul li .txt p{
  color: #444;
  padding-top: .1rem;
  font-weight: 500;
  font-size: .26rem;
  line-height: .34rem;
}
</style>



