<template>
  <div class="goods">
    <div class="left" ref="left">
      <ul>
        <li v-if="getData.container_operation_source" @click="clickScrool(0)" :class="{'current':currentIndex === 0}">
          <span><img :src="getData.container_operation_source.tag_icon" alt=""></span>
          {{getData.container_operation_source.tag_name}}
        </li>
        <li v-for="(food,index) in getData.food_spu_tags" @click="clickScrool(index+1)" :class="{'current':currentIndex === index+1}">
          <span  v-if="food.icon"><img :src="food.icon"></span><p>{{food.name}}</p><i v-show="shangPinShu(food.spus)">{{shangPinShu(food.spus)}}</i>
        </li>
      </ul>
    </div>
    <div class="right" ref="right">
      <ul>
        <li v-if="getData.container_operation_source" class="food-list-hook">
          <div class="img" v-for="(item,index) in getData.container_operation_source.operation_source_list" :key="index"><img :src="item.pic_url"></div>
        </li>
        <li v-for="(item,index) in getData.food_spu_tags" :key="index" class="foots food-list-hook">
          <h3>{{item.name}}</h3>
          <ul>
            <li v-for="(item2,index) in item.spus" :key="index" @click.stop="clickItem(item2)">
              <div class="r-left">
                <div class="img foot-img"><img :src="item2.picture"></div>
                <div>
                  <h2>{{item2.name}}</h2>
                  <p>{{item2.month_saled_content}}<i></i>{{item2.praise_content}}</p>
                  <div class="img tuijian-img" v-if="item2.product_label_picture"><img :src="item2.product_label_picture"></div>
                  <p><span>¥{{item2.min_price}}</span>/{{item2.unit}}</p>
                </div>
              </div>
              <cartcontrol :item="item2"></cartcontrol>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--购物车-->
    <shop-cart :foods="gouWuChe"></shop-cart>
    <!--商品详情-->
    <shop-details :food="shopDetails" ref="showFood"></shop-details>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import ShopCsrt from './shopcart'
  import Cartcontrol from  './cartcontrol'
  import ShopDetails from './ShopDetails'
    export default {
        name: "Goods",
      components:{
          'shop-cart':ShopCsrt,
          'cartcontrol':Cartcontrol,
          'shop-details':ShopDetails,
      },
      data () {
          return {
            listheight:[],
            left:{},
            right:{},
            scrollY:0,
            shopDetails:{},
            //getData:{},
          }
      },
      methods:{
          initScroll(){
            this.left = new BScroll(this.$refs.left,{click:true});
            this.right = new BScroll(this.$refs.right,{probeType:3,click:true});

            //滚动监听事件
            this.right.on("scroll",(pos) => {
              this.scrollY = Math.abs(Math.round(pos.y));
            })
          },
          clickItem(item){
            this.shopDetails = item;
            this.$refs.showFood.showView();
          },
          shangPinShu(food){
            let shu = 0;
            food.forEach((myfood) => {
              if (myfood.count > 0){
                shu += myfood.count;
              }
            })
            return shu;
          },
          getHeight(){
            let foodList = this.$refs.right.getElementsByClassName('food-list-hook');
            let height = 0;
            this.listheight.push(height);

            for (let i=0; i<foodList.length; i++){
              height += foodList[i].clientHeight;
              this.listheight.push(height);
            }
          },
          clickScrool(index){
            let foodList = this.$refs.right.getElementsByClassName('food-list-hook');
            let element = foodList[index];
            this.right.scrollToElement(element,500)
          },
      },
      computed:{
          getData(){
            return this.$store.getters.getHeader
          },
          gouWuChe(){
            let foods = [];
            if (this.getData.food_spu_tags){
              this.getData.food_spu_tags.forEach((myFoods) => {
                myFoods.spus.forEach((food) => {
                  if(food.count > 0){
                    foods.push(food);
                  }
                })
              });
            }
            return foods;
          },
          currentIndex(){
            for (let i=0; i<this.listheight.length; i++){
              let height1 = this.listheight[i];
              let height2 = this.listheight[i + 1];

              if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)){
                return i;
              }
            }
            return 0;
          }
      },
      created(){
        // this.$axios.get("/goods")
        //   .then((data) => {
        //     this.getData = data.data.data;
        //   })

        this.$nextTick(() => {
          setTimeout(() => {
            this.getHeight();
            this.initScroll();
          },1000)
        })
      },
      // watch:{
      //   ceshi:function () {
      //     console.log(111)
      //     this.$nextTick(() => {
      //       this.getHeight();
      //       this.initScroll();
      //     })
      //   }
      // },
    }
</script>

<style scoped>
.goods{
  display: flex;
  width: 100%;
  position: absolute;
  top: 4.1rem;
  bottom: 1rem;
  overflow: hidden;
}
  .left{
    flex: 0 0 1.8rem;
    height: 100%;
    background: #f5f5f5;
  }
  .left ul li{
    display: flex;
    padding: .35rem .2rem;
    border-bottom: .02rem solid #e8e8e8;
    position: relative;
  }
  .left ul li i{
    background-color: #fb2020;
    border-radius: 1rem;
    position: absolute;
    right: .08rem;
    top: .08rem;
    color: #fff;
    font-weight: bold;
    font-size: .2rem;
    text-align: center;
    line-height: .25rem;
    padding: 0 .05rem;
  }
  .left .current{
    background-color: #fff;
    font-weight: 500;
  }
  .left ul li p{
    line-height: .32rem;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-weight: 500;
    color: #444;
    overflow : hidden;
  }
  .left ul li span{
    display: inline-block;
    padding-right: .08rem;
    vertical-align: middle;
  }
  .left ul li span img{
    width: .32rem;
    height: .32rem;
  }
  .right{
    flex: 1;
  }
  .right ul li{
    padding: .2rem;
  }
  .right ul li ul li{
    position: relative;
  }
  .right ul li h3{
    font-size: .3rem;
    font-weight: 500;
    color: #444;
    position: relative;
  }
  .right ul li h3:before{
    content: "";
    width: .05rem;
    border-radius: .1rem;
    height: .26rem;
    background-color: #ffbb22;
    display: inline-block;
    margin-right: .12rem;
  }
  .right .img{
    border-radius: .1rem;
    overflow: hidden;
    height: auto;
  }
  .right .img:nth-child(2){
    margin-top: .2rem;
  }
  .right .img img{
    width: 100%;
  }
  .foots li{
    padding: .2rem 0!important;
    display: flex;
  }
  .foots li .r-left{
    display: flex;
  }
  .foots li .r-left h2{
    color: #444;
    font-size: .32rem;
    font-weight: 600;
    line-height: .5rem;
    padding-bottom: .16rem;
  }
  .foots li .r-left p{
    color: #999;
    font-weight: 400;
    padding-bottom: .1rem;
  }
  .foots li .r-left p i{
    padding: 0 .16rem;
  }
  .foots li .r-left p span{
    font-weight: 600;
    font-size: .32rem;
    color: #ff8322;
  }

  .right .foot-img{
    width: 1.5rem;
    margin-right: .2rem;
  }
  .right .tuijian-img{
    width: 1.3rem;
    margin-bottom: .1rem;
  }
</style>



