<template>
  <div class="seller">
    <div class="top">
      <div class="dizhi">
        <div>
          <span></span>
          <p>{{seller.address}}</p>
        </div>
        <div class="phone">
          <span></span>
        </div>
      </div>
      <div class="img" ref="picsView">
        <ul v-if="seller.poi_env" ref="picsList">
          <li v-for="(img,index) in seller.poi_env.thumbnails_url_list" :key="index" ref="picsItem"><img :src="img"></li>
        </ul>
      </div>
      <div class="dangan">
        <div>
          <span></span>
          <p>查看食品安全档案</p>
        </div>
        <div>
          <span class="font_family icon-keyboard_arrow_right"></span>
        </div>
      </div>
    </div>
    <div class="main">
      <div>
        <span></span>
        <p>配送服务：{{seller.app_delivery_tip}}</p>
      </div>
      <div>
        <span></span>
        <p>配送时间：{{seller.shipping_time}}</p>
      </div>
    </div>
    <div class="bottom">
      <div class="b-top">
        <div>
          <span></span>
          <p>商家服务</p>
        </div>
        <div v-if="seller.poi_service">
          <span><img :src="seller.poi_service[0].icon"></span>
          <p>{{seller.poi_service[0].content}}</p>
        </div>
      </div>
      <div class="b-bottom" v-if="seller.discounts2">
        <span><img :src="seller.discounts2[0].icon_url"></span>
        <p>{{seller.discounts2[0].info}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
    export default {
        name: "Seller",
      data (){
          return {
            seller:{},
          }
      },
      created(){
          this.$axios.get('/seller')
            .then((data) => {
              if(data.status === 200){
                this.seller = data.data.data;

                this.$nextTick(() => {
                  if (this.seller.poi_env.thumbnails_url_list){
                    let imgW = this.$refs.picsItem[0].clientWidth;
                    let width = imgW * this.seller.poi_env.thumbnails_url_list.length;
                    this.$refs.picsList.style.width = width + 'px';
                    this.scroll = new BScroll(this.$refs.picsView,{scrollX:true})
                  }
                })
              }
              console.log(data)
            })
      },
    }
</script>

<style scoped>
img{
  width: 100%;
  height: 100%;
}
.seller{
  background-color: #f7f5f7;
  width: 100%;
  position: absolute;
  top: 4.1rem;
  bottom: 0;
}
.bottom,
.main,
.top{
  background-color: #fff;
  border-top: .2rem solid #f7f5f7;
  color: #444;
}
.bottom .b-top,
.bottom .b-bottom,
.main div,
.top .dangan,
.top .dizhi{
  display: flex;
  justify-content: space-between;
  margin: 0 .2rem;
  padding: .23rem 0;
  border-bottom: .01rem solid #f7f7f7;
  font-weight: bold;
  font-size: .24rem;
}
.top .dangan{
  border-top: .01rem solid #f7f7f7;
  border-bottom: none;
}
.bottom .b-bottom,
.bottom .b-top,
.main div{
  justify-content: left;
}
.bottom .b-top div,
.top .dangan div:nth-child(1),
.top .dizhi div:nth-child(1){
  display: flex;
}
.bottom .b-top div:nth-child(2){
  padding-left: .4rem;
}
.bottom div span,
.main div span,
.top .dangan div:nth-child(1) span,
.top .dizhi div:nth-child(1) span{
  display: inline-block;
  width: .25rem;
  height: .3rem;
  background: url(../../common/img/address.png) no-repeat;
  overflow: hidden;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.top .dangan div:nth-child(1) span{
  background: url(../../common/img/safety.png) no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.main div:nth-child(1) span{
  background: url(../../common/img/delivery.png) no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.main div:nth-child(2) span{
  width: .3rem;
  background: url(../../common/img/time.png) no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.bottom .b-top div:nth-child(1) span{
  width: .3rem;
  background: url(../../common/img/server.png) no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.bottom .b-top div:nth-child(2) span{
  width: .3rem;
  background: #fff;
}
.bottom .b-bottom span{
  width: .3rem;
  border-radius: .04rem;
}
.bottom div p,
.main div p,
.top .dangan div:nth-child(1) p,
.top .dizhi div:nth-child(1) p{
  padding: .04rem 0 0 .2rem;
}
.top .dizhi .phone {
  padding: 0 .2rem 0 .4rem;
  border-left: .01rem solid #ccc;
}
.top .dangan div:nth-child(2) span,
.top .dizhi .phone span{
  width: .3rem;
  height: .3rem;
  display: inline-block;
  background: url(../../common/img/phone.png) no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
}
.top .dangan div:nth-child(2) span{
  background: #fff;
  font-weight: 500;
}
.top .img ul{
  overflow: hidden;
  padding: .2rem 0 .2rem .2rem;
  white-space: nowrap;
}
.top .img ul li{
  display: inline-block;
  padding-right: .2rem;
  width: 1.8rem;
  height: 1.5rem;
  overflow: hidden;
}
</style>



