<template>
    <div class="all">
      <div class="table-bottom">
        <ul>
          <li v-for="(item,index) in getRatings.labels" :key="index" :class="{'chapin':item.label_star <= 0}">{{item.content}}{{item.label_count}}</li>
        </ul>
      </div>
      <ul class="all-ul">
        <li v-for="(item,index) in getRatings.comments" :key="index">
          <div class="img">
            <img :src="item.user_pic_url">
          </div>
          <div class="txt">
            <div>
              <span>{{item.user_name}}</span>
              <span>{{item.comment_time}}</span>
            </div>
            <p>评分<the-star :star="item.order_comment_score"></the-star></p>
            <p>{{item.comment}}</p>
            <span v-if="item.comment_pics.length" v-for="img in item.comment_pics"><img :src="img.thumbnail_url"></span>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
  import TheStar from '../TheStar'
    export default {
        name: "RatingAll",
      components:{
          'the-star':TheStar,
      },
      computed:{
          getRatings(){
            return this.$store.getters.getRatings;
          },
      },
    }
</script>

<style scoped>
  img{
    width: 100%;
    height: 100%;
  }
  .all .all-ul li{
    display: flex;
    padding: .3rem 0 .4rem 0;
    justify-content: space-between;
    border-bottom: .01rem solid #efefef;
    margin: 0 .2rem;
  }
  .all .all-ul li .img{
    width: .8rem;
    height: .8rem;
    border-radius: 2rem;
    background-color: #e8e8e8;
    overflow: hidden;
  }
  .all .all-ul li .txt{
    padding-left: .2rem;
    flex: 1;
    padding-top: .1rem;
  }
  .all .all-ul li .txt div{
    display: flex;
    justify-content: space-between;
    color: #444;
    font-size: .24rem;
  }
  .all .all-ul li .txt p{
    color: #444;
    padding-top: .1rem;
    font-weight: 500;
    font-size: .26rem;
    line-height: .34rem;
  }
  .all .all-ul li .txt p:nth-child(2){
    font-weight: 400;
    font-size: .24rem;
    line-height: .32rem;
    color: #888;
    display: flex;
    padding: .1rem 0;
  }
  .all .all-ul li .txt p:nth-child(2) .star{
    margin-top: .02rem
  }
  .all .all-ul li .txt span:nth-child(4){
    display: inline-block;
    width: 2.8rem;
    height: 3.6rem;
    overflow: hidden;
    border-radius: .04rem;
    margin-top: .15rem;
  }

  .table-bottom{
    padding: 0 .2rem;
  }
  .table-bottom ul{
    display: flex;
  }
  .table-bottom li{
    display: inline-block;
    padding: .1rem .12rem;
    border-radius: .04rem;
    background-color: #f7f4f7;
    font-size: .24rem;
    color: #444;
    margin-right:.1rem;
  }
  .table-bottom .chapin{
    color: #adadad;
  }
</style>



