<template>
  <div id="app">
    <app-header></app-header>
    <app-nav></app-nav>
    <router-view></router-view>
  </div>
</template>

<script>
  import Headers from './components/header/Header'
  import Nav from './components/Nav'

export default {
  name: 'App',
  components: {
    'app-header':Headers,
    'app-nav':Nav,
  },
  created:function () {
    this.$axios.get("/ratings")
      .then((data) => {
        this.$store.commit("pushRatings",data.data.data)
        console.log(data.data.data)
      })
  },
}
</script>

<style>

</style>



