<template>
  <div>
    <div class="shopcart" v-if="getPoiInfo">
      <div class="left">
        <div class="shopcart-icon" :class="{'active':shangPinShu}" @click="clickShopCar">
          <span class="font_family icon-shopping_cart"></span>
          <i v-show="shangPinShu" class="shangpinshu">{{shangPinShu}}</i>
        </div>
        <div class="jiage">
          <p v-show="shangPinJia" class="shangpinjia">¥{{shangPinJia}}</p>
          <p :class="{'peison':shangPinJia}">另需{{getPoiInfo.shipping_fee_tip}}</p>
        </div>
      </div>
      <div class="right" :class="{'active':shangPinShu}">{{quJieSuan}}</div>

      <transition name="move">
        <div class="shopCarList" v-show="bgShow">
          <div class="youhui">新用户立减17元</div>
          <div class="kodai">
            <div class="list-left">1号口袋</div>
            <div class="list-right" @click="clearAll"><span></span>清空购物车</div>
          </div>
          <div class="list" ref="shopCarScroll">
            <ul>
              <li v-for="item in foods">
                <div class="list-li-left">
                  <div>
                    <h5>{{item.name}}</h5>
                    <p>{{item.unit}}</p>
                  </div>
                  <div>¥{{item.min_price*item.count}}</div>
                </div>
                <cartcontrol :item="item"></cartcontrol>
              </li>
            </ul>
          </div>
        </div>
      </transition>

    </div>
    <div class="show" v-show="bgShow" @click="clickBg"></div>
  </div>
</template>

<script>
  import Cartcontrol from './cartcontrol'
  import BScroll from 'better-scroll'
    export default {
        name: "shopcart",
      props:["foods"],
      components:{
          'cartcontrol':Cartcontrol,
      },
      data(){
          return {
            shopCar: true,
          }
      },
      computed:{
          getPoiInfo(){
            return this.$store.getters.getHeader.poi_info;
          },
        quJieSuan(){
            if(this.shangPinShu > 0){
              return "去结算";
            }else{
              return this.getPoiInfo.min_price_tip;
            }
        },
        shangPinShu(){
            let shu = 0;
            this.foods.forEach((food) => {
              shu += food.count;
            })
          return shu;
        },
        shangPinJia(){
            let jia = 0;
            this.foods.forEach((food) => {
              jia += food.count * food.min_price;
            })
          return jia;
        },
        bgShow(){
            if (!this.shangPinShu){
              this.shopCar = true;
              return false;
            }
            let show = !this.shopCar;

            if(show){
              this.$nextTick(() => {
                if(!this.shopScroll){
                  this.shopScroll = new BScroll(this.$refs.shopCarScroll,{click:true})
                }else{
                  this.shopScroll.refresh();
                }
              })
            }

            return show;
        },
      },
      methods:{
        clickShopCar(){
          if(!this.shangPinShu){
            return;
          }
          this.shopCar = !this.shopCar;
        },
        clearAll(){
          this.foods.forEach((item) => {
            item.count = 0;
          })
        },
        clickBg(){
          this.shopCar = true;
        },
      },
    }
</script>

<style scoped>
.shopcart{
  width: 100%;
  height: 1rem;
  background-color: #514f4f;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  z-index: 99;
  color: rgba(255,255,255,.9);
  justify-content: space-between;
}
.left{
  display: flex;
  position: relative;
  padding-left: .3rem;
}
.left .shopcart-icon{
  width: 1.1rem;
  height: 1.1rem;
  background-color: #6b6969;
  border-radius: 50%;
  position: absolute;
  bottom: .2rem;
  text-align: center;
}
.left .shopcart-icon span{
  font-size: .6rem;
  line-height: 1.1rem;
}
.left .jiage{
  line-height: .45rem;
  padding-left: 1.25rem;
}
  .right{
    font-size: .3rem;
    font-weight: 500;
    line-height: 1rem;
    padding: 0 .5rem;
  }
  .shangpinshu{
    width: .3rem;
    height: .3rem;
    position: absolute;
    text-align: center;
    line-height: .3rem;
    right: .006rem;
    border-radius: 50%;
    background-color: red;
    font-weight: bold;
    font-size: .2rem;
  }
  .shangpinjia{
    font-size: .35rem;
    font-weight: 500;
    padding-top: .12rem;
    line-height: .35rem;
  }
  .jiage p:nth-child(2){
    line-height: 1rem;
  }
  .peison{
    line-height: .35rem!important;
  }
  .active{
    background-color: #ffbb22!important;
    color: #444;
    font-weight: bold;
  }
.active i,
.active span{
  color: rgba(255,255,255,.9);
}
  .shopCarList{
    position: absolute;
    width: 100%;
    overflow: hidden;
    background-color: #fff;
    bottom: 1rem;
    left: 0;
    z-index: -1;
  }
  .shopCarList .youhui{
    width: 100%;
    height: .5rem;
    background-color: #fee9cb;
    color: #666;
    text-align: center;
    line-height: .5rem;
    font-weight: 500;
    font-size: .26rem;
  }
  .shopCarList .kodai{
    width: 100%;
    height: .5rem;
    background-color: #f6f1f6;
    line-height: .5rem;
    display: flex;
    justify-content: space-between;
    color: #444;
    font-weight: 500;
    font-size: .24rem;
  }
  .shopCarList .kodai .list-left{
    border-left: .08rem solid #5cbf31;
    padding-left: .1rem;
  }
  .shopCarList .kodai .list-right{
    padding-right: .2rem;
  }
  .shopCarList .kodai .list-right span{
    display: inline-block;
    width: .26rem;
    height: .26rem;
    background-image: url(../../common/img/ash_bin.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    vertical-align: -9%;
    margin-right: .1rem;
  }
  .shopCarList .list{
    width: 100%;
    overflow: hidden;
    max-height: 6rem;

  }
  .shopCarList .list ul li{
    display: flex;
    justify-content: space-between;
    color: #444;
    overflow: hidden;
    position: relative;
    padding: .2rem 0 .2rem .2rem;
  }
  .shopCarList .list .list-li-left{
    flex: 1;
    display: flex;
    justify-content: space-between;
    padding-right: 2.5rem;
    font-weight: 500;
  }
  .shopCarList .list .list-li-left h5{
    font-size: .3rem;
  }
  .shopCarList .list .list-li-left p{
    color: #999;
    font-weight: 400;
    font-size: .24rem;
    padding-top: .1rem;
  }
  .shopCarList .list .list-li-left div:nth-child(2){
    line-height: .6rem;
  }
  .show{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,.4);
    width: 100%;
    height: 100%;
    z-index: 1;
  }
</style>



