// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import Axios from 'axios'

import './common/css/reset.css'
import './common/css/border.css'
import './common/fonts/iconfont.css'

import Goods from './components/content/Goods'
import Ratings from './components/content/Ratings'
import Seller from './components/content/Seller'

import RatingAll from './components/content/ratingTable/RatingAll'
import RatingImg from  './components/content/ratingTable/RatingImg'
import RatingDian from  './components/content/ratingTable/RatingDian'

import {store} from './store/store'

Vue.config.productionTip = false

Axios.defaults.baseURL = "https://www.easy-mock.com/mock/5c1717a0bf8dda63f572aa61/meiTuan";
Axios.defaults.headers.get['Content-Type'] = "application/json";

Vue.use(VueRouter)
Vue.prototype.$axios = Axios

const router = new VueRouter({
  routes:[
    {path:'/',redirect:'/goods'},
    {path:'/goods',component:Goods},
    {path:'/ratings',redirect:'/ratings/all',component:Ratings,children:[
        {path:'/ratings/all',component:RatingAll,name:'ratingAll'},
        {path:'/ratings/img',component:RatingImg,name:'ratingImg'},
        {path:'/ratings/dian',component:RatingDian,name:'ratingDian'},
      ]},
    {path:'/seller',component:Seller},
    {path:'*',redirect:"/"}
  ],
  mode:"history",
  linkActiveClass:"active",
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store:store,
  components: { App },
  template: '<App/>'
})



