<template>
  <div class="headers" :style="{backgroundImage:'url('+ headerData.head_pic_url +')'}">
    <header-top></header-top>
    <header-bottom v-bind:headerData="headerData"></header-bottom>
  </div>
</template>

<script>
  import HeaderTop from './HeaderTop'
  import HeaderBottom from './HeaderBottom'

    export default {
        name: "headers",
      components:{
        'header-top':HeaderTop,
        'header-bottom':HeaderBottom,
      },
      data(){
        return {
          headerData:{},
        }
      },
      created:function () {
        this.$axios.get("/goods")
          .then((data) => {
            // console.log(data.data.data)
            this.headerData = data.data.data.poi_info;
            this.$store.commit('pushGoods',data.data.data)
          })
        this.$axios.get('https://www.easy-mock.com/mock/5c1717a0bf8dda63f572aa61/meiTuan/seller')
          .then((data) => {
            console.log(data)
          })
      }
    }
</script>

<style scoped>
.headers{
  height: 3.3rem;
  background-repeat: no-repeat;
  background-size: 100% auto;
  overflow: hidden;
}
</style>



