<template>
    <div class="nav">
      <router-link to="/goods" class="nav-item">点餐<i class="line"></i></router-link>
      <router-link to="/ratings" class="nav-item">评价({{getCommentNum}})<i class="line"></i></router-link>
      <router-link to="/seller" class="nav-item">商家<i class="line"></i></router-link>
    </div>
</template>

<script>
    export default {
        name: "Nav",
      computed:{
          getCommentNum(){
            return this.$store.getters.getRatings.comment_num;
          },
      },
    }
</script>

<style scoped>
.nav{
  display: flex;
  width: 100%;
  border-bottom: .01rem solid #e4e4e4;
}
  .nav-item{
    flex: 1;
    text-align: center;
    font-size: .3rem;
    color: #666;
    position: relative;
    padding: .2rem 0 .3rem 0;
  }
  .nav .active{
    color: #ffbb22;
    font-weight: 500;
  }
  .nav .active .line{
    width: .4rem;
    height: .04rem;
    display: inline-block;
    background-color: #ffbb22;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -.2rem;
  }
</style>



