<template>
    <div class="top">
      <div class="icon">
        <span class="font_family icon-arrow_lift"></span>
      </div>
      <div class="sousuo">
        <span></span>
        <input type="text" placeholder="搜索店内商品">
      </div>
      <div class="right">
        <span>拼单</span>
        <span class="font_family icon-weibiaoti5"></span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "HeaderTop",

    }
</script>

<style scoped>
.top{
  display: flex;
  justify-content: space-between;
  padding-top: .4rem;
  color: rgba(255,255,255,.94);
}
.icon{
  text-align: center;
  line-height: .7rem;
  padding: 0 .3rem;
}
.icon span{
  font-size: .36rem;
}
  .sousuo{
    display: flex;
    flex: auto;
    height: .7rem;
    padding: 0 .2rem;
    border-radius: .4rem;
    background-color: rgba(255,255,255,.8);
  }
  .sousuo span{
    width: .38rem;
    height: .38rem;
    display: inline-block;
    background-image: url(../../common/img/search.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    margin-top: .18rem;
    margin-right: .05rem;
  }
  .sousuo input{
    width: 100%;
    height: 100%;
    background: none;
  }
  .right{
    padding: 0 .2rem 0 .4rem;
    line-height: .7rem;
  }
  .right span:nth-child(1){
    padding: .01rem .1rem;
    border: .01rem solid #ffffff;
    border-radius: .04rem;
  }
  .right span:nth-child(2){
    font-size: .4rem;
    padding: 0 0 0 .1rem;
    vertical-align: middle;
  }
</style>



