<template>
    <div class="headerBottom">
      <div class="top">
        <div class="left">
          <div><img :src="headerData.pic_url" alt=""></div>
          <h5>{{headerData.name}}</h5>
        </div>
        <div class="right">
          <div><img src="../../common/img/star.png" alt=""></div>
          <p>收藏</p>
        </div>
      </div>
      <div class="bottom">
        <div v-for="discounts in headerData.discounts2">
          <div><img :src="discounts.icon_url" alt=""></div>
          <p>{{discounts.info}}</p>
        </div>
        <div v-if="headerData.discounts2" @click="show">
          <p>{{headerData.discounts2.length}}个活动</p>
          <span class="font_family icon-keyboard_arrow_right"></span>
        </div>
      </div>
      <transition name="slide-fade">
        <div class="huodon" v-show="tanchuan">
          <div class="tanchuan" :style="{backgroundImage:'url('+ headerData.poi_back_pic_url +')'}">
            <div class="tanTop">
              <div v-if="headerData">
                <div class="img"><img :src="headerData.pic_url" alt=""></div>
                <h6>{{headerData.name}}</h6>
                <div class="xingji">
                  <thestar :star="headerData.wm_poi_score"></thestar>
                  <p>{{headerData.wm_poi_score}}</p>
                </div>
              </div>
              <div>
                <div>{{headerData.min_price_tip}}<i>|</i>{{headerData.shipping_fee_tip}}<i>|</i>{{headerData.delivery_time_tip}}</div>
                <p>配送时间：{{headerData.shipping_time}}</p>
              </div>
            </div>
            <div class="tanBottom" v-if="headerData.discounts2">
              <div><img :src="headerData.discounts2[0].icon_url" alt=""></div>
              <p>{{headerData.discounts2[0].info}}</p>
            </div>
            <div class="guanbi" @click="show">
              <span class="font_family icon-close"></span>
            </div>
          </div>
        </div>
      </transition>
    </div>
</template>

<script>
  import TheStar from '../content/TheStar'
    export default {
        name: "HeaderBottom",
      data () {
          return {
            tanchuan: false,
          }
      },
      components:{
          'thestar':TheStar,
      },
      props:['headerData'],
      methods:{
          show(){
            this.tanchuan = !this.tanchuan;
          },
      }
    }
</script>

<style scoped >
  .headerBottom{
    padding: .25rem;
  }
  .headerBottom .top{
    display: flex;
    justify-content: space-between;
  }
  .headerBottom .top .left{
    display: flex;
  }
  .headerBottom .top .left h5{
    color: #fff;
    font-size: .35rem;
    font-weight: 600;
    line-height: 1.2rem;
    padding-left: .2rem;
  }
.headerBottom .top .left div{
  width: 1.2rem;
  height: 1.2rem;
  overflow: hidden;
  border-radius: .15rem;
  position: relative;
}
.headerBottom .top .left div img{
  height: 100%;
  position: absolute;
  top: 0;
  left: -.2rem;
}
  .headerBottom .top .right{
    color: #fff;
    text-align: center;
    padding-top: .2rem;
  }
  .headerBottom .top .right div{
    width: .4rem;
    overflow: hidden;
    padding: 0 0 .1rem .1rem;
  }
  .headerBottom .top .right div img{
    width: 100%;
  }
  .headerBottom .top .right p{
    font-weight: 500;
    font-size: .3rem;
  }
  .headerBottom .bottom{
    padding-top: .18rem;
    color: rgba(255,255,255,.97);
    display: flex;
    justify-content: space-between;
  }
  .headerBottom .bottom div:nth-child(1){
    display: flex;
    width: 80%;
  }
  .headerBottom .bottom div:nth-child(2){
    display: flex;
    line-height: .36rem;
    font-size: .25rem;
  }
  .headerBottom .bottom div:nth-child(1) div{
    width: .36rem;
    height: .36rem;
    overflow: hidden;
  }
  .headerBottom .bottom div:nth-child(1) div img{
    width: 100%;
    height: 100%;
  }
  .headerBottom .bottom div:nth-child(1) p{
    line-height: .36rem;
    height: .36rem;
    padding-left: .1rem;
    width: 90%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(.15rem);
    opacity: 0;
  }
  .huodon{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 999;
    transition: all .5s ease;
  }
  .huodon .tanchuan{
    position: absolute;
    margin: 1rem .3rem;
    min-height: 70%;
    min-width: 78%;
    border-radius: .2rem;
    background-repeat: no-repeat;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    padding: .5rem;
    background-color: #cccccc;
  }
  .huodon .tanchuan .tanTop{
    width: 100%;
    border-bottom: .02rem solid rgba(255,255,255,.5);
    text-align: center;
    position: relative;
    overflow: hidden;
    padding-bottom: .35rem;
  }
  .huodon .tanchuan .tanTop div:nth-child(1){
    overflow: hidden;
  }
  .huodon .tanchuan .tanTop .img{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: .2rem;
    overflow: hidden;
    position: relative;
    left: 50%;
    margin-left: -.7rem;
    margin-top: 10%;
  }
  .huodon .tanchuan .tanTop div:nth-child(1) h6{
    font-size: .35rem;
    color: rgba(255,255,255,.8);
    font-weight: 600;
    padding-top: .3rem;
  }
  .huodon .tanchuan .tanTop div:nth-child(1) .xingji{
    padding-top: .2rem;
    padding-left: 1.88rem;
    display: flex;
    color: rgba(255,255,255.9);
    font-weight: 500;
  }
  .huodon .tanchuan .tanTop div:nth-child(2){
    font-weight: 500;
    color: rgba(255,255,255,.6);
    padding-top: .2rem;
  }
  .huodon .tanchuan .tanTop div:nth-child(2) i{
    padding: 0 .2rem;
  }
  .huodon .tanchuan .tanTop div:nth-child(2) p{
    font-size: .24rem;
    padding-top: .2rem;
  }
  .huodon .tanchuan .tanTop .img img{
    height: 100%;
    position: absolute;
    left: -.2rem;
  }
  .huodon .tanchuan .tanBottom{
    display: flex;
    font-size: .24rem;
    color: rgba(255,255,255,.9);
    font-weight: 500;
    padding: .4rem 0 0 .05rem;
  }
  .huodon .tanchuan .tanBottom p{
    padding-top: .04rem;
    padding-left: .1rem;
  }
  .huodon .tanchuan .tanBottom div{
    width: .32rem;
    height: .32rem;
    overflow: hidden;
    border-radius: .04rem;
  }
  .huodon .tanchuan .tanBottom div img{
    width: 100%;
  }
  .huodon .guanbi{
    position: absolute;
    left: 50%;
    bottom: -1rem;
    width: .8rem;
    height: .8rem;
    border-radius: 50%;
    background-color: rgba(0,0,0,.2);
    color: #fff;
    text-align: center;
    margin-left: -.4rem;
  }
  .huodon .guanbi span{
    font-size: .36rem;
    padding-top: .21rem;
    display: inline-block;
  }
</style>



