<template>
    <div class="star">
      <ul>
        <li v-for="star in stars()"><img :src="star" alt=""></li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "TheStar",
      props:["star"],
      methods:{
        stars(){
          var fen = this.star;
          var arr = [];
          for (var i=0; i<5; i++){
            if(fen >= 1){
              arr.push(require('../../common/img/star24_on@2x.png'));
            }else if (fen < 1 && fen > 0){
              arr.push(require('../../common/img/star24_half@2x.png'));
            }else{
              arr.push(require('../../common/img/star24_off@2x.png'));
            }
            fen -= 1;
          }
          return arr;
        },
      },
    }
</script>

<style scoped>
.star ul{
  display: flex;
  padding-right: .12rem;
}
.star ul li{
  padding: 0 .04rem;
  width: .22rem;
  height: .22rem;
  display: flex;
}
.star ul li img{
  width: 100%;
}
</style>



