<template>
  <div class="ratings" ref="ratingView">
    <div>
      <div class="r-top" v-if="getRatings.scores">
        <div class="left">
          <div class="l-left">
            <h5>{{getRatings.scores.comment_score}}</h5>
            <p>商家评分</p>
          </div>
          <div class="l-right">
            <div>
              <p>口味</p>
              <thestar :star="getRatings.scores.quality_score"></thestar>
              <span>{{getRatings.scores.quality_score}}</span>
            </div>
            <div>
              <p>包装</p>
              <thestar :star="getRatings.scores.pack_score"></thestar>
              <span>{{getRatings.scores.pack_score}}</span>
            </div>
          </div>
        </div>
        <div class="right">
          <h5>{{getRatings.scores.delivery_score}}</h5>
          <p>配送评分</p>
        </div>
      </div>
      <div class="r-bottom">
        <rating-table :ratings="getRatings"></rating-table>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import TheStar from '../content/TheStar'
  import RatingTable from './ratingTable/RatingTable'
    export default {
        name: "Ratings",
      components:{
          'thestar':TheStar,
          'rating-table':RatingTable,
      },
      computed:{
          getRatings(){
            return this.$store.getters.getRatings;
          },
      },
      created(){
        this.$nextTick(() => {
          if(!this.shopScroll){
            this.shopScroll = new BScroll(this.$refs.ratingView,{click:true})
          }else{
            this.shopScroll.refresh();
          }
        })
      },
    }
</script>

<style scoped>
.ratings{
  overflow: hidden;
  position: absolute;
  bottom: 0;
  top: 4.1rem;
  width: 100%;
}
.ratings .r-top{
  display: flex;
  padding: .32rem .2rem;
  border-bottom: .2rem solid #f7f5f7;
}
.ratings .r-top .left{
  display: flex;
  flex: 1;
}
.ratings .r-top .left .l-right{
  padding: .1rem 0 .1rem .4rem;
}
.ratings .r-top .left .l-right p{
  padding-right: .05rem;
}
.ratings .r-top .left .l-right div{
  display: flex;
  font-size: .24rem;
}
.ratings .r-top .left .l-right div span{
  color: #ff8322;
  font-weight: 500;
}
.ratings .r-top .left .l-right div:nth-child(1){
  padding-bottom: .15rem;
}
.ratings .r-top .left{
  text-align: center;
  padding: 0 .3rem 0 .2rem;
  color: #444;
}
.ratings .r-top .left .l-left h5{
  color: #ff8322;
  font-weight: 600;
}
.ratings .r-top .right{
  padding: 0 .2rem 0 .3rem;
  border-left: .01rem solid #eee;
  color: #a5a5a5;
  text-align: center;
}
.ratings .r-top .left h5,
.ratings .r-top .right h5{
  font-size: .4rem;
  font-weight: 500;
  padding-bottom: .1rem;
}
</style>



