import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state:{
    goods:{},
    ratings:{},
  },
  getters:{
    getHeader(state){
      return state.goods
    },
    getRatings(state){
      return state.ratings
    },
  },
  mutations:{
    pushGoods(state,data){
      state.goods = data;
    },
    pushRatings(state,data){
      state.ratings = data;
    }
  },
});



